import {vNav} from "../../../components";
export default {
  name: '',
  components: {vNav},
  data () {
    return {
      info: {
        avatar: '',
        not_token_num: '0',
        jifen: "0",   //  等接口 改
        id: "888888",
        user_nicename: 'user_name',
        balance: "0"
      },
      token: null,
    }
  },
  created(){
    let [token, id] = [this.cookie.get("token"), this.cookie.get('id')];
    if (token && id) {
      this.loading.show();
      this.token = token;
      this.ajax('get_info', `id=${id}&token=${token}`, this.init_info);
    }
  },
  methods: {
    init_info(d){
      this.loading.hide();
      if (d.code === 200) {
        this.info = d.data;

        this.info.balance = d.data.balance;
        this.cookie.set('balance', d.data.balance);
        this.cookie.set('username', d.data.user_nicename);
        this.cookie.set('avatar', d.data.avatar);
        this.cookie.set("id",d.data.id);

      } else this.toast(d.descrp || "链接服务器失败");
    },
    go(page){
      let p = '';
      if (this.token) p = page;
      else p = "login";
      this.$router.push(p);
    }
  }
}



